import { Component, OnInit } from '@angular/core';
import {Injectable} from '@angular/core';
import {IpServer} from '../ip.server';
import {HttpClient} from '@angular/common/http';

@Component({
  selector: 'app-thumbnails',
  templateUrl: './thumbnails.component.html',
  styleUrls: ['./thumbnails.component.css'] ,
  providers:  [IpServer]
})
@Injectable()
export class ThumbnailsComponent implements OnInit {
  activeIndex = 0;
  dataIMG: {
    'thumbnails': [{
      'imgUrl': '',
      'bodyText': '',
      'title': ''
      }]
  };
  Url = 'https://gist.githubusercontent.com/DZuz14/56b7f363e9787fb4c0240eb145bc2b9f/raw/e0b67d18352704d155929b7e27e0dae08cc383b6/data.json';

  renderActiveImage(id: number): void{
      this.activeIndex = id;
  }

  constructor(private ipServer: IpServer) {
   this.ipServer.getJson(this.Url).toPromise().then(res => this.dataIMG = res.thumbnails).then(res => console.log(res));
  }
  ngOnInit(): void {}

}
